.jumbotron {
    background-image: url(/static/media/blur-books-ceramic-176103.e1ef1b90.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: 200px;
    color: wheat;
    text-shadow: 2px 2px black;
}

.lead {
    font-size: 2.5em;
}

@media only screen and (max-width: 960px) {
    .display-4 {
        font-size: 2.5em;
    }
    .lead {
        font-size: 1.5em;
    }
}
@media only screen and (max-width: 540px) {
    .display-4 {
        font-size: 1.5em;
    }
    .lead {
        font-size: 1.25em;
    }
}
.description {
    max-height: 100px;
}

@media only screen and (max-width: 540px) {
    .book-title {
        font-size: .70em
    }
    .description {
        max-height: 80px;
        font-size: .6em;
    }
    .btn {
        font-size: .65em
    }
}
.active {
    font-weight: bold;
}
